module.exports = [{"contributors":[{"username":"georgeshanti","email":"georgeshanti@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":1,"createdAt":"2018-10-02T10:11:56.737Z","updatedAt":"2018-10-16T14:17:31.252Z"}],"createdBy":{"username":"georgeshanti","email":"georgeshanti@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":1,"createdAt":"2018-10-02T10:11:56.737Z","updatedAt":"2018-10-16T14:17:31.252Z"},"updatedBy":{"username":"pallavi","email":"pallavivg98@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":3,"createdAt":"2018-10-16T14:17:34.761Z","updatedAt":"2018-10-16T14:31:29.337Z"},"name":"MECLABS","type":"Tech","codename":"meclabs","details":"<b>\"To stay ahead, you must have ideas imbued in the wings!\"</b><br/><p>In the drive to 'Bridge the Realms', the 19th edition of EXCEL presents MECLABS,  a project-making event exclusively for MECians. From 'Fancy Lights' to 'Faraday's Guitar', it's fun to meddle with technology as you fine-tune your skills! Unlike never before, this year, the event has been revamped, and is now open for both 2nd and 3rd-year students!! Get ready to 'whet your wits' as you form your dream team of innovators, and unleash the creativity in you.</p><p>Engineering isn’t exclusively limited to listening to lectures and jotting down notes. Get ready to dirty your hands and build projects with assistance from knowledgeable mentors. Effectuate your ideas into reality and get the platform to exhibit your project.</p><p><b>Date : </b>4th Nov<br /><b>Venue : </b>301, 314, 315, Circuits Lab</p> <br><br> <b>Event Coordinators</b> <p>Varun Narayanan - +91 8089625814</p> <p>Mathew Thomas - +91 9645902751</p>","img":"https://excelmec.org/static/uploads/images/meclabs.jpg","imgs":[],"id":3,"createdAt":"2018-10-15T15:51:38.687Z","updatedAt":"2018-10-18T17:17:17.885Z"},{"contributors":[{"username":"navinmohan","email":"navinmohan.mec@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":2,"createdAt":"2018-10-16T03:20:47.193Z","updatedAt":"2018-10-16T14:31:17.389Z"}],"createdBy":{"username":"navinmohan","email":"navinmohan.mec@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":2,"createdAt":"2018-10-16T03:20:47.193Z","updatedAt":"2018-10-16T14:31:17.389Z"},"updatedBy":{"username":"navinmohan","email":"navinmohan.mec@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":2,"createdAt":"2018-10-16T03:20:47.193Z","updatedAt":"2018-10-16T14:31:17.389Z"},"name":"IBeTo","type":"Tech","codename":"ibeto","website":"https://ibeto.excelmec.org","details":"<p>IBeTo (Innovations for a Better Tomorrow) is the flagship event of Excel 2018, by the credence that technology holds the key to unlocking society’s most unnerving challenges. The 7th edition of IBeTo seeks to replicate and augment the immense responses received from numerous colleges all over India, bringing forward some of the most spectacular innovations that benefit society. The brightest of these ideas are recognized and provided mentorship to bring about innovative end products every year. Futhermore, the winner is awarded a cash prize of Rs 1 lakh. IBeTo thus aspires to unite social relevance and technical innovation, to the pioneer and the populace.</p>","img":"https://ibeto.excelmec.org/img/ibeto-logo.png","imgs":[],"id":4,"createdAt":"2018-10-17T12:18:03.822Z","updatedAt":"2018-10-17T12:25:17.739Z"},{"contributors":[{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"}],"createdBy":{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"},"updatedBy":{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"},"name":"#KochiPlogs","type":"Non Tech","codename":"plogging","details":"<p>Plogging is essentially a practice of jogging while picking up litter.Combining the fundamental concepts of promoting fitness and cleaning up the environment, Plogging is the perfect testament to “Bridging Realms”, the principle on which Excel 2018 thrives. The event was conducted on 28th October, at Streetscape, Panampilly Nagar.</p><p>Excel 2018 aims to be the sole college fest in India to introduce and promote Plogging, intending to provide a commendable reach for the activity and its potential impact of developing the society.<br />Banking on the fact that Plogging is being tagged as the latest fad in exercising, all over the world, Excel wishes to encourage and adopt the concept of eco- friendly workout sessions in Kerala.</p><p>Plogging has very high promising health benefits as it is not just about running, but also incorporates cardio workouts like stretching, bending, etc for picking up litter.</p><p>The conclusion event, planned, was a “plogging party” that involves light breakfast and entertainment for the ploggers. A community was built among the ploggers to promote the plogging activity in the days to come.</p><p>Ms Sana Khader, Mrs Saumini Jayan, Kochi Mayor and several other distinguished dignitaries witnessed this new trend of jogging.</p><br /><b>Rules and Regulations</b><p><ul><li>The event will include a maximum of 600 ploggers.</li><li>The event will be conducted from Streetscape, Panampilly Nagar to Central Park, Panampilly Nagar covering a distance of 2.5km on 28th October, 2018.</li><li>Ploggers will be given biodegradable bags and gloves for healthy and effeicient plogging.</li><li>The event will be concluded by a plogging party at Central Park, Panampilly Nagar where all the ploggers will be formed as a community and will be provided with light breakfast. Entertainment programmes will be conducted.</li><li>The ploggers can register the event through an online portal where they can enter T-shirt size and they can avail food coupons for the Plogging party.</li></ul></p><p><b>Date : </b>28th October<br /></p><br /><b>Event Coordinators</b><p>Roshan Sunny : +91 9497244043<br/>Noel Mathew Manoj : +91 8589957006</p>","img":"https://excelmec.org/static/uploads/images/Photo from H@rith@.jpg","imgs":[],"id":5,"createdAt":"2018-10-17T17:05:23.693Z","updatedAt":"2018-10-18T17:09:52.340Z"},{"contributors":[{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"}],"createdBy":{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"},"updatedBy":{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"},"name":"Zero Hour Tech Class","type":"Tech","codename":"zero-hour-tech-class","details":"<p>The Zero Hour-Tech classes is an initiative taken by the Social Initiatives team of EXCEL 2018. In this program, students of the college dispersed themselves and visited various schools to talk on a socially relevant topic combined with its technical aspects. This year, the topic chosen was “Internet, Your Best Friend” for students of grade VII. An hour long session brought into the awareness that students can benefit from Internet in a numerous ways.</p><p>The class first began with a brief introduction as to what exactly Internet is and how it enhances its reach until today. Then, it proceeded into its significance, pros and cons, and also the various fields conquered and made simpler with its use.</p><p>The session ended with an interactive session with the students where their doubts were raised and cleared.</p><p>This session took place in 10 schools,across the state of Kerala.</p></p><p>The schools are :</p><p><ul><li>Cochin Refineries School, Ambalamugal, Ernakulam</li><li>Christava Mahilalayam Public School, Ernakulam</li><li>Lourdes Public School, Kottayam</li><li>GUPS, Mankada, Pallipuram, Malapuram</li><li>MGM Public School, Kolayad, Kannur</li><li>Carmel Higher Secondary School, Chalakudy, Thrissur</li><li>St. Mary’s Girls High School, Edathua, Pathanamthitta</li><li>Christ Nagar Senior Secondary School, Thiruvallom, Trivandrum</li><li>Silver Hills Higher Secondary School,Kozhikode</li><li>Bhavan's Munshi Vidyashram, Thiruvamkulam, Ernakulam</li></ul></p><br /><b>Social Initiatives Manager | Excel 2018</b><p>Haritha Paul : +91 98469 48875</p>","img":"https://excelmec.org/static/uploads/images/Zero Hour Tech Class.jpg","imgs":[],"id":6,"createdAt":"2018-10-17T17:32:40.059Z","updatedAt":"2018-10-17T18:28:50.405Z"},{"contributors":[{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"}],"createdBy":{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"},"updatedBy":{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"},"name":"Sixth Sense Robotics Workshop","type":"Tech","codename":"sixth-sense-robotics-workshop","details":"<p>Interested in Robotics or Embedded Systems? Excel 2018 presents Sixth Sense, an Arduino-based robotics workshop just for you! Sixth Sense technology allows you to control the world in a simpler fashion using things like natural gestures and has a wide scope of application in fields like automation. This workshop will introduce you to the amazing world of Sixth Sense technology and its intriguing applications.</p><p>This one-day workshop will teach you robotics and MATLAB programming to make a Sixth Sense Robot that can take its own decisions without any external interference, using image capturing, image processing and colour recognition. You will learn to make your own robot in the span of a day, with comprehensive material and one-on-one attention from the trainers! A kit will be provided to you, which will have everything you need to make the robot. </p><p><b>What is in the kit?</b><ul><li>Arduino Circuit board</li><li>Programmer Cable</li><li>Motor Driver L293D Module</li><li>Robotics Chassis BO DC Motors</li><li>BO Wheels</li><li>L Clamps</li><li>Castor wheel</li><li>Jumper wires</li><li>Battery Snapper</li><li>9V DC battery</li><li>Screw Set</li><li>Screw driver</li></ul><b>What you will learn :</b><ul><li>Details about microcontrollers.</li><li>Programming a microcontroller using the Arduino interface.</li><li>Interfacing and controlling various devices like LED, motors, sensors etc with a microcontroller.</li><li>MATLAB Programming with Arduino.</li><li>Making various types of robots including their algorithms and coding.</li><li>Interfacing of USB camera</li></ul><b>Details :</b><ul><li>Date: November 2nd, 2018</li><li>Time: 9:30 a.m. to 4:30 p.m.</li><li>Fees: ₹600 for MEC students, ₹750 for non MEC students</li></ul></p><b>To Register,</b><br /><a style =\"color:inherit\" href=\"https://www.yepdesk.com/sixth-sense-robotics\">Follow this link</a></p><p><b>Date : </b>2nd November<br /><b>Venue : </b>Media Hall</p><p><br /><b>Event Coordinators</b><p>Shilpa Rajesh Nair : +91 94954 06729<br />Joann Elza : +91 89434 24456</p></p>","img":"https://excelmec.org/static/uploads/images/sixth_sense.png","imgs":[],"id":8,"createdAt":"2018-10-17T18:50:32.419Z","updatedAt":"2018-10-18T17:14:31.858Z"},{"contributors":[{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"}],"createdBy":{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"},"updatedBy":{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"},"name":"Unmasked","type":"Non Tech","codename":"unmasked","details":"<p>Time and space become meaningless for those who control the flow. Brace yourselves for brilliance unimagined, for your senses could very well deceive you.</p><p>Get ready for some world class acts of mentalism and illusion at Unmasked, featuring Psychological Illusionist Arjun Guru. Arjun Guru is an Illusionist who fuses principles of magic and deception with psychology, to create illusions in your mind. He specializes in blending psychology, mentalism and sleight of hand, and is known to customize every show he takes on to create an unforgettable experience.</p><p>In a span of 2 years, Arjun has performed at various colleges including BITS Goa, IIT Delhi and SPA and various big companies like the Taj Group of hotels.</p><br /><b>Blink, and you miss it. Limited seats.</b><br /><p>Register now at : <a style = \"color: inherit\" href=\"https://www.instamojo.com/excel2018/unmasked-excel-2018/\">https://www.instamojo.com/excel2018/unmasked-excel-2018/</a></p><br /><p><b>Date : </b>3rd November<br /><b>Venue : </b>Amphitheater</p><br /><b>Talks Manager</b><p>Amrin Kareem : +91 8891888969</p><b>Event Coordinator</b><p>Aravind R Krishna : +91 75598 27553</p>","img":"https://excelmec.org/static/uploads/images/unmaskedicon2.png","imgs":[],"id":9,"createdAt":"2018-10-17T19:12:40.777Z","updatedAt":"2018-10-18T17:14:07.112Z"},{"contributors":[{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"}],"createdBy":{"username":"shriram","email":"shriramvenugopal123@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":5,"createdAt":"2018-10-17T09:29:44.989Z","updatedAt":"2018-10-17T09:31:05.286Z"},"updatedBy":{"username":"pallavi","email":"pallavivg98@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":3,"createdAt":"2018-10-16T14:17:34.761Z","updatedAt":"2018-10-16T14:31:29.337Z"},"name":"(UN)TITLED","type":"Non Tech","codename":"untitled","details":"<p>The undisputed spotlight event of Excel and one of the most popular talk events in Kochi, (UN)TITLED brings to light the journeys of people who have \"been there, done that.\" Previous speakers include Mr. Sijo Kuruvila George, Founding CEO, Startup Village, WeTech Qualcomm Scholar and TEDx Speaker Ms. Arya Murali, Kerala’s most popular stand-up comedian Mr. Vinay Menon, Ms. Ruby Peethambaran, co-founder of Fourth Ambit, young maker Saarang Sumesh, renowned dancer Ms. Priya Kumar and many others.<br />This year, (UN)TITLED is centred around the theme ‘The Secret Ingredient’. A gamut of speakers share the secret ingredient to their recipe that landed them on the road to glory.</p><p>The panel this year :<br /><ol><li><b>Nayana Shyam</b><p>Who said one can’t do everything?</p><p>(UN)TITLED presents <b>Nayana Shyam</b>, writer-director-actor working with the ever famous FilterCopy. From scripting Things Brother and Sister Do to filming Vidya Balan for FilterCopy, she has come a long way! Wish to find out what her secret ingredient of success is?<br />Don’t miss <b>(UN)TITLED 2018</b> where Nayana reveals her magical recipe for success. Get a taste of her creativity in <a style=\"color:inherit\" href=\"https://www.youtube.com/watch?v=oo-c1xw_oJE\">Fashion Conversations with Mom</a></p></li><li><b>Anaida Stanly</b><p>\"Time is like a handful of sand, the tighter you grasp it, the faster it runs through your fingers.\"</p><p>Presenting a creative being who can shape sand into meaning: <b>Anaida Stanly</b>, an animation student at Rajagiri Institute who is also an amazing sand artist. Witness her magic as she shares her story of how passion found her and helped her overcome all odds to bring happiness in the world through the works of her hands. Be there at <b>(UN)TITLED 2018</b> to witness creativity in all its glory.</p></li><li><b>Vicky Roy</b><p>\"Each time the shutter clicks, another magical moment gets frozen in time.\"</p><p>Obtain a new perspective on life and discover how Vicky Roy went from being a rag picker in the streets of Delhi to becoming a world renowned photographer. Embark on a journey of colours, tales and memories with Vicky Roy, the internationally acclaimed photographer, in <b>(UN)TITLED</b>.</p></li><li><b>Vishak Nair</b><p>“Give an artist a canvas, for you'll be surprised to see what comes out!” </p><p>His charm and his intriguing style; Vishak Nair has donned multiple hats famously for his stint with stand-up comedy, direction and acting.Creativity in multiple dimensions! Catch a glimpse of his excellence at <b>(UN)TITLED</b> as he moves the audience in an enthralling vibe!</p></li><li><b>Bhaghya Babu</b><p>Cartoons take us into another dimension where thoughts and ideas . A graphic designer by profession, Bhaghya Babu has brought out a lot of laughter through her extremely popular Instagram account Awkwerrrd. Through her unique style of conveying Malayali situations in a hilarious package, she is the cool inspiration for many. Be there at Excel 2018 presents <b>(UN)TITLED</b> to hear how the artist came, saw and conquered our hearts.</p></li></ol></p><br /><p><b>To Register,</b><br /><a style =\"color:inherit\" href=\"https://www.instamojo.com/excel2018/untitled-excel-2018-non-mec/\">Follow this link</a></p><p><b>Date : </b>2nd November<br /><b>Venue : </b>Amphitheater</p><br /><b>Talks Manager</b><p>Amrin Kareem : +91 8891888969</p><b>Event Coordinator</b><p>Archana Jayachandran : +91 9061393569</p>","img":"https://excelmec.org/static/uploads/images/Untitledicon.png","imgs":[],"id":10,"createdAt":"2018-10-17T19:49:36.347Z","updatedAt":"2018-10-22T15:21:18.016Z"},{"contributors":[{"username":"pallavi","email":"pallavivg98@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":3,"createdAt":"2018-10-16T14:17:34.761Z","updatedAt":"2018-10-16T14:31:29.337Z"}],"createdBy":{"username":"pallavi","email":"pallavivg98@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":3,"createdAt":"2018-10-16T14:17:34.761Z","updatedAt":"2018-10-16T14:31:29.337Z"},"updatedBy":{"username":"neha","email":"nehamj798@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":6,"createdAt":"2018-10-17T14:40:55.094Z","updatedAt":"2018-10-17T14:41:55.880Z"},"name":"Pulse 18","type":"Tech","codename":"pulse18","details":"<i>The impact of biomedical engineering is so vast that even life support systems for scientists going to Mars are built by those in this revolutionary field!</i>  <p>As a part of EXCEL 2k18, we present before you, the most distinctive and spellbinding event to celebrate biomedical engineering,  <b>PULSE 18.</b><br> Come over to explore the engineering of life.</p>  <p>An opportunity like no other for budding engineers to gain practical knowledge  through numerous project exhibits, and biomedical equipments. Active on the 2nd and 3rd of November, winning the Online Quiz O.W.L, gets you a direct entry to the highlight of the event,<i> the Obscura Maze</i>.<br>The Biomedical Exhibition, Pulse'18 is up and runnit this Nov 2 and 3.</p>  <br> <b>Event Coordinators</b> <p>Karthik Manoj - +91 9544272065</p> <p>Anjali Sunil - +91 8547746060</p>","img":"https://excelmec.org/static/uploads/images/pulse.jpeg","imgs":[],"id":11,"createdAt":"2018-10-18T17:18:00.325Z","updatedAt":"2018-10-26T17:49:50.990Z"},{"contributors":[{"username":"neha","email":"nehamj798@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":6,"createdAt":"2018-10-17T14:40:55.094Z","updatedAt":"2018-10-17T14:41:55.880Z"}],"createdBy":{"username":"neha","email":"nehamj798@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":6,"createdAt":"2018-10-17T14:40:55.094Z","updatedAt":"2018-10-17T14:41:55.880Z"},"updatedBy":{"username":"neha","email":"nehamj798@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":6,"createdAt":"2018-10-17T14:40:55.094Z","updatedAt":"2018-10-17T14:41:55.880Z"},"createdAt":"2018-10-23T08:58:57.801Z","name":"Guest Lectures","codename":"guest-lectures","type":"Non-tech","details":"<p><ol><li><b>Anmol Rodriguez</b><br><i>Inspiration to some. Saviour to many.</i><br>The lady with an iron fist in a velvet glove, she proves to the world that beauty begins the moment you decide to love yourself.<br><b>Excel 2018 presents the Guest Lecture Series.</b> Get ready to get inspired by <b>Anmol Rodriguez</b>, the girl who refused to give up on her dreams just because she was told to do so.<br>To witness her radiance in all its glory, give Aunty Ji a watch:<br><a style =\"color:inherit\" href=\"https://www.youtube.com/watch?v=euQUrkgeZnA\">https://www.youtube.com/watch?v=euQUrkgeZnA</a></li><br><br><li><b>Arun Balachandran<br><i>Pioneer. Visionary. Trendsetter.</i></b><br>Be it the seamless enthusiasm to support innovation in all its forms or the will to venture into new platforms with the drive to excel, <b>Arun Balachandran</b> has been an inspiration to many  budding entrepreneurs and techies alike. <br><b>Excel 2018 presents the Guest Lecture Series.</b>Be there to listen to his story of passion and progress.</li><br><li><b>Mehar MP<br><i>It takes creative minds to make the best of today’s technologies, and those who connect the two are the leaders of tomorrow.</i></b><br>TinkerHub is the best example of how cutting edge technologies are made accessible for the today’s youth. Mehar founded this non-profit to bridge the gap between opportunity and capability, and so many lives have been enhanced due to his do-good attitude. His projects have taken the right steps to connect and produce better leaders of tomorrow.<br><b>Excel 2018 presents the Guest Lecture Series.</b>Discover how you can take a step forward to achieve your dreams and goals in life.<br></b></li></ol><br><b>To Register</b><br><a style =\"color:inherit\" href=\"https://goo.gl/forms/P5oJh4JyskEI5IPD2\">Follow this link</a></p>","img":"https://excelmec.org/static/uploads/images/Poster10.png","imgs":[],"buttons":"","updatedAt":"2018-10-30T14:12:15.695Z","id":12},{"contributors":[{"username":"pallavi","email":"pallavivg98@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":3,"createdAt":"2018-10-16T14:17:34.761Z","updatedAt":"2018-10-16T14:31:29.337Z"}],"createdBy":{"username":"pallavi","email":"pallavivg98@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":3,"createdAt":"2018-10-16T14:17:34.761Z","updatedAt":"2018-10-16T14:31:29.337Z"},"updatedBy":{"username":"navinmohan","email":"navinmohan.mec@gmail.com","lang":"en_US","template":"default","id_ref":"1","provider":"local","id":2,"createdAt":"2018-10-16T03:20:47.193Z","updatedAt":"2018-10-16T14:31:17.389Z"},"createdAt":"2018-10-29T13:22:51.162Z","name":"Synapse","type":"Non Tech","codename":"synapse","details":"<p><i>Abiding by the principles of Coulter, we have decided to make the first day of Excel count.</i></p>  <p>Beckman Coulter, one of the leading biomedical industries, presents before you, one of the most awaited talk sessions of the year. Listen to Mr Sarith P Antony, Senior team leader at Beckman Coulter speak exclusively only at Excel 2018. </p> <p>Grab this amazing opportunity to know more about the research opportunities and innovations emerging in the biomedical industry.</p> <b>Delegates:</b> <p>Mr. Mahadeven Krishna Iyer, <i>Regional Service Manager - South India</i></p> <p>Mr. Annup Babu<i>, Sales Manager - Kerala</i></p> <p>Mr. Sarith P Antony<i>, Senior Team Leader - Service, Kerala</i></p><br> <b>Event Coordinators</b> <p>Pavithra Kumar B - +91 9526068336</p> <p>Catherine vargheese - +91 9847092549</p>","website":"","img":"https://excelmec.org/static/uploads/images/synapse (1).jpeg","updatedAt":"2018-10-30T05:10:46.295Z","id":13,"buttons":[{"name":"Register Here","link":"https://goo.gl/forms/AMtcyzF8jYFX8Q9w1"}],"imgs":[]}]

